import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import ErrorMessage from '../views/ErrorMessage';

export default class App {
  constructor() {
    this.state = {
      baseSearchURL: 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=',
      searchURL: '',
      baseVideoListURL: 'https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=',
    };
    this.page = 0;

    this.model = new AppModel(this.state);

    this.data = null;

    this.view = new AppView(this.data);

    this.error = new ErrorMessage('Connection error!');
  }

  async start() {
    this.data = await this.model.getClipNames();

    this.view.render(this.data);

    document.getElementById('search-button').addEventListener('click', this.search.bind(this));
    document.getElementById('search-input').addEventListener('keypress', this.pressEnter.bind(this));

    if (this.data !== null && this.data !== 'error') {
      document.getElementById('next-page-button').addEventListener('click', this.nextPage.bind(this));
      document.getElementById('prev-page-button').addEventListener('click', this.prevPage.bind(this));
      document.getElementById('first-page-button').addEventListener('click', this.firstPage.bind(this));
    }
  }

  nextPage() {
    this.view.clipList.nextPage();
  }

  prevPage() {
    this.view.clipList.prevPage();
  }

  firstPage() {
    this.view.clipList.firstPage();
  }

  pressEnter(event) {
    if (event.code === 'Enter') {
      event.preventDefault();
      this.search.call(this);
    }
  }

  async search() {
    const request = document.getElementById('search-input').value;
    if (request) {
      this.state.searchURL = this.state.baseSearchURL + request;
    }

    this.start();
  }
}
