export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static extractClipNames(data) {
    function cutDescriptions(clip) {
      const maxDescLength = 90;
      const maxTitleLength = 40;
      const newClip = clip;
      if (clip.snippet.description.length > maxDescLength) {
        newClip.snippet.description = `${newClip.snippet.description.slice(0, maxDescLength)}...`;
      }
      if (clip.snippet.title.length > maxTitleLength) {
        newClip.snippet.title = `${newClip.snippet.title.slice(0, maxTitleLength)}...`;
      }

      return newClip;
    }

    if (data !== null) {
      return data.items.map(cutDescriptions);
    }

    return null;
  }

  // async getStatics(data) {
  //   const url = `${this.state.baseVideoListURL}
  //   ${data.items.map(clip => clip.id.videoId).join(',')}&part=statistics,snippet`;
  //   const newData = await fetch(url);
  //   return newData;
  // }

  async getClipNames() {
    const { searchURL } = this.state;

    let data = null;

    if (searchURL !== '') {
      try {
        let responce = await fetch(searchURL);
        data = await responce.json();
        const url = `${this.state.baseVideoListURL}
                      ${data.items.map(clip => clip.id.videoId).join(',')}
                      &part=statistics,snippet`;
        responce = await fetch(url);
        data = await responce.json();
      } catch (e) {
        return 'error';
      }
    }

    return AppModel.extractClipNames(data);
  }
}
