export default class {
  constructor(message) {
    this.message = message;
  }

  show() {
    const warning = document.createElement('div');
    warning.id = 'warning';
    warning.className = 'warning';
    warning.innerHTML = this.message;
    document.body.appendChild(warning);
    // alert('d');
    function zoomIn() {
      document.getElementById('warning').style.transform = 'scale(1.5)';
    }
    function fade() {
      document.getElementById('warning').style.transform = 'scale(0)';
    }
    setTimeout(zoomIn, 1);
    setTimeout(fade, 2000);
  }
}
