import SearchBar from '../SearchBar';
import ClipsList from '../ClipsList';
import ErrorMessage from '../ErrorMessage';

export default class AppView {
  constructor(titles) {
    this.titles = titles;
    this.searchBar = new SearchBar();
    this.clipList = new ClipsList(titles);
    this.error = new ErrorMessage('connection promlem!');
  }

  render(data) {
    this.searchBar.render();
    this.clipList.render(data);
    if (data === 'error') {
      this.error.show();
    }
  }
}
