export default class ClipsList {
  constructor(data) {
    this.data = data;
    this.page = 0;
  }

  render(data) {
    this.data = data;
    if (this.data !== null && this.data !== 'error') {
      const content = document.createElement('ul');
      content.id = 'content';
      content.className = 'content';
      content.innerHTML = data.map(clip => `
      <li class="clip">
        <a href="https://youtu.be/${clip.id.videoId}" target="_blank">
          <div class="title-container"><h2>${clip.snippet.title}</h2></div>
          <img src="${clip.snippet.thumbnails.medium.url}" alt="preview" width="100%">
          <ul class="statistics">
            <li>${clip.statistics.viewCount}</li>
            <li>${clip.snippet.publishedAt}</li>
            <li>${clip.snippet.channelTitle}</li>
          </ul>
          <p>${clip.snippet.description}</p>
        </a>
      </li>
      `).join('');

      document.body.appendChild(content);

      document.body.innerHTML = `${document.body.innerHTML}
      <div id="navigation-container" class="container">
        <div id="first-page-button" class="first page-button"></div>
        <div id="prev-page-button" class="prev page-button"></div>
        <div id="curr-page-indicator" class="curr page-button"></div>
        <div id="next-page-button" class="next page-button"></div>
      </div>`;

      if (!this.page) {
        document.getElementById('first-page-button').style.visibility = 'hidden';
        document.getElementById('prev-page-button').style.visibility = 'hidden';
      }

      document.getElementById('curr-page-indicator').innerHTML = this.page + 1;
    } else {
      const content = document.createElement('h1');
      content.innerHTML = 'YouTube Client';

      document.body.appendChild(content);
    }
  }

  nextPage() {
    this.page = this.page + 1;
    document.getElementById('content').style.transform = `translateX(${-100 * this.page}vw)`;

    document.getElementById('first-page-button').style.visibility = 'visible';
    document.getElementById('prev-page-button').style.visibility = 'visible';

    document.getElementById('curr-page-indicator').innerHTML = `<span>${this.page + 1}</span>`;
  }

  prevPage() {
    this.page = this.page - 1;
    if (this.page < 0) this.page = 0;
    document.getElementById('content').style.transform = `translateX(${-100 * this.page}vw)`;

    document.getElementById('curr-page-indicator').innerHTML = `<span>${this.page + 1}</span>`;

    if (!this.page) {
      document.getElementById('first-page-button').style.visibility = 'hidden';
      document.getElementById('prev-page-button').style.visibility = 'hidden';
    }
  }

  firstPage() {
    this.page = 0;
    document.getElementById('content').style.transform = `translateX(${-100 * this.page}vw)`;

    document.getElementById('curr-page-indicator').innerHTML = `<span>${this.page + 1}</span>`;

    document.getElementById('first-page-button').style.visibility = 'hidden';
    document.getElementById('prev-page-button').style.visibility = 'hidden';
  }
}
